* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
  height: 20px;
}
.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #e21b1a;
  background: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-underline {
  border-color: #e21b1a;
}
.PayState {
  background: #fff;
}
.PayState-box {
  padding: 12px 0;
  -webkit-box-shadow: inset 0 0 0 0 #F0F0F0;
          box-shadow: inset 0 0 0 0 #F0F0F0;
  text-align: center;
}
.PayState-box img {
  height: 60px;
}
.PayState-box-tips {
  margin-top: 12px;
  font-size: 16px;
  color: #e21b1a;
}
.PayState-order {
  border-top: 1px solid #f1f1f1;
  border-bottom: 1px solid #f1f1f1;
  padding: 20px;
}
.PayState-order .orderItem {
  line-height: 16px;
  letter-spacing: 0;
  font-size: 13px;
  color: #999;
}
.PayState-order .orderItem:nth-child(2) {
  margin: 10px 0;
}
.PayState-order .orderItem span {
  color: #151515;
}
.PayState-opear {
  height: 110px;
}
.PayState-opear .am-button {
  height: 36px;
  line-height: 36px;
  font-size: 14px;
}
.PayState-opear .am-button.payBtn {
  margin-right: 80px;
  border: 1px solid #e21b1a;
  color: #e21b1a;
}
.PayState-opear .am-button.payBtn::before {
  border: none;
}



