* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
  height: 20px;
}
.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #e21b1a;
  background: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-underline {
  border-color: #e21b1a;
}
.Vanity .Carousel_Img {
  background: #fff;
}
.Vanity .vanity-num .am-tabs-default-bar-tab {
  font-size: 13px;
  color: #666;
}
.Vanity .vanity-num .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.Vanity .vanity-num .am-tabs-default-bar {
  background: #fff !important;
}
.Vanity .vanity-num-list {
  padding: 10px;
}
.Vanity .vanity-num-list .listItem {
  margin-bottom: 10px;
  width: calc(50% - 5px);
  background: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: center;
}
.Vanity .vanity-num-list .listItem:nth-child(2n) {
  margin-left: 10px;
}
.Vanity .vanity-num-list .listItem em {
  color: #e21b1a;
}
.Vanity .vanity-num-list .listItem-num {
  padding: 10px 0;
  line-height: 33px;
  font-size: 24px;
  color: #2a2a2a;
}
.Vanity .vanity-num-list .listItem-price {
  padding: 5px 0;
  line-height: 17px;
  background: #fff;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  font-size: 12px;
  color: #999;
}



