* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
