* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
  height: 20px;
}
.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #e21b1a;
  background: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-underline {
  border-color: #e21b1a;
}
.SelectAddress .right-label {
  font-size: 16px;
  color: #333;
}
.SelectAddress .am-radio {
  padding: 2.5px;
  border: 1px solid #ccc;
  border-radius: 50%;
}
.SelectAddress .am-radio.am-radio-checked {
  border: 1px solid #e21b1a;
  background: #e21b1a;
}
.SelectAddress .am-radio.am-radio-checked .am-radio-inner::after {
  width: 5px;
  height: 10px;
  top: -1px;
  right: 7px;
  border-color: #fff;
}
.SelectAddress .am-list-item .am-list-content {
  font-size: 14px;
  color: #181818;
}
.SelectAddress .am-list-item .am-list-content span {
  float: right;
}
.SelectAddress .am-list-item .am-list-content .am-list-brief {
  line-height: 18px;
  white-space: initial;
  font-size: 12px;
  color: #666;
}



