* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
  height: 20px;
}
.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #e21b1a;
  background: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-underline {
  border-color: #e21b1a;
}
.OrderDetail .base-content {
  height: calc(100vh - 95px);
}
.OrderDetail .base-content .order-item {
  background: #fff;
}
.OrderDetail .base-content .flex-item {
  padding: 15px 15px 0;
}
.OrderDetail .base-content .flex-item:last-of-type {
  padding-bottom: 15px;
}
.OrderDetail .base-content .flex-item .labelItem {
  font-size: 13px;
  color: #b0b0b0;
}
.OrderDetail .base-content .flex-item .labelItem.orderCon {
  font-size: 14px;
}
.OrderDetail .base-content .flex-item .labelItem.orderPrice {
  color: #e21b1a;
}
.OrderDetail .base-content .flex-item.orderTit {
  font-size: 16px;
  color: #e21b1a;
}
.OrderDetail .base-content .order-address {
  padding: 15px;
}
.OrderDetail .base-content .order-address .addr-icon {
  margin-right: 20px;
}
.OrderDetail .base-content .order-address .addr-icon .addrIco {
  margin-top: -15px;
  height: 20px;
}
.OrderDetail .base-content .order-address .addr-info {
  line-height: 16px;
  font-size: 14px;
}
.OrderDetail .base-content .order-address .addr-info .addr-ess {
  margin-top: 10px;
  line-height: 20px;
  font-size: 13px;
  color: #333;
}
.OrderDetail .base-content .goods-info {
  padding: 10px;
}
.OrderDetail .base-content .goods-info .goods-img {
  width: 75px;
  height: 75px;
  margin-right: 2px;
}
.OrderDetail .base-content .goods-info .title {
  padding-top: 5px;
  line-height: 20px;
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 14px;
  color: #333;
}
.OrderDetail .base-content .goods-info .bottom-info {
  margin-top: 15px;
}
.OrderDetail .base-content .goods-info .bottom-info .spec {
  font-size: 14px;
  color: #999;
  height: 22px;
  line-height: 22px;
}
.OrderDetail .base-content .goods-info .goods-refurn {
  margin-top: 8px;
}
.OrderDetail .base-content .goods-info .goods-refurn .am-button {
  border-radius: 2px;
  color: #333;
}
.OrderDetail .base-content .goods-info .goods-refurn .am-button::before {
  border: 1px solid #f5f5f5;
}
.OrderDetail .base-content .goods-info .price {
  padding-top: 8px;
  font-size: 13px;
  font-weight: 600;
  text-align: right;
  color: #333;
}
.OrderDetail .footer {
  padding: 0 15px;
  height: 49px;
  background: #fff;
  border-top: 1px solid #f1f1f1;
}
.OrderDetail .footer .am-button-ghost {
  border-radius: 2px;
  color: #000;
}
.OrderDetail .footer .am-button-ghost::before {
  border: 1px solid #f5f5f5;
  border-radius: 2px;
}
.OrderDetail .footer .contact,
.OrderDetail .footer .look-log,
.OrderDetail .footer .eva-order {
  margin-left: 15px;
}
.OrderDetail .footer .eva-order {
  color: #e21b1a;
}
.OrderDetail .footer .eva-order::before {
  border: 1px solid #e21b1a;
  border-radius: 2px;
}



