* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
  height: 20px;
}
.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #e21b1a;
  background: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-underline {
  border-color: #e21b1a;
}
.Pay .base-content {
  padding: 15px;
}
.Pay-box {
  background: #fff;
  border-radius: 8px;
}
.Pay-box-goods {
  padding: 20px 15px;
}
.Pay-box-goods .goodsImg {
  width: 66px;
  height: 66px;
}
.Pay-box-goods .goodsImg img {
  width: 100%;
  display: block;
}
.Pay-box-goods .goodsInfo {
  width: calc(100% - 76px);
  height: 66px;
}
.Pay-box-goods .goodsInfo-tit {
  line-height: 20px;
  font-size: 13px;
  color: #181818;
}
.Pay-box-goods .goodsInfo-num {
  margin-top: 10px;
  line-height: 13px;
}
.Pay-box-goods .goodsInfo-num span {
  font-size: 13px;
  color: #999;
}
.Pay-box-goods .goodsInfo-num span:first-of-type {
  font-size: 14px;
  color: #e21b1a;
}
.Pay-box-mode {
  padding: 20px 15px;
  background: #fbfbfb;
}
.Pay-box-mode .pay-check {
  margin-top: 23px;
}
.Pay-box-mode .pay-check .am-flexbox {
  margin-bottom: 19px;
}
.Pay-box-mode .pay-check .am-flexbox:last-of-type {
  margin-bottom: 0;
}
.Pay-box-mode .pay-check .am-flexbox-item {
  font-size: 13px;
}
.Pay-box-mode .pay-check .am-flexbox-item:last-of-type {
  -ms-flex: inherit;
      flex: inherit;
  text-align: right;
}
.Pay-box-mode .pay-check .am-flexbox-item img {
  height: 22px;
}
.Pay-box-mode .pay-check .am-flexbox-item span {
  line-height: 13px;
}
.Pay-box-mode .pay-check .am-flexbox-item .pay-gold {
  letter-spacing: 0;
  color: #404143;
}
.Pay-box-mode .pay-check .am-flexbox-item .account-money {
  margin-left: 10px;
  color: #999;
}
.Pay-box-mode .pay-check .am-flexbox-item .payMoney {
  margin-right: 8px;
  color: #e21b1a;
}
.Pay-box-mode .pay-check .am-radio {
  padding: 2.5px;
  border: 1px solid #ccc;
  border-radius: 50%;
}
.Pay-box-mode .pay-check .am-radio.am-radio-checked {
  border: 1px solid #e21b1a;
  background: #e21b1a;
}
.Pay-box-mode .pay-check .am-radio.am-radio-checked .am-radio-inner::after {
  width: 5px;
  height: 10px;
  top: -1px;
  right: 7px;
  border-color: #fff;
}
.Pay-box-opear {
  padding: 30px 15px;
}
.Pay-box-opear .am-button {
  width: 100%;
  height: 40px;
  line-height: 40px;
}
.Pay-box-opear .am-button span {
  font-size: 16px;
}



