* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
  height: 20px;
}
.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #e21b1a;
  background: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-underline {
  border-color: #e21b1a;
}
.ShopCart .base-content {
  height: calc(100vh - 95px);
}
.ShopCart .base-content .nodata {
  padding: 40px;
  font-size: 16px;
  color: #999;
  text-align: center;
}
.ShopCart .base-content .right-label {
  font-size: 16px;
  color: #333;
}
.ShopCart .base-content .store-item {
  margin-bottom: 10px;
}
.ShopCart .base-content .store-info {
  padding: 10px 15px;
  background: #fff;
}
.ShopCart .base-content .store-info img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
}
.ShopCart .base-content .store-info .store-name {
  font-size: 14px;
}
.ShopCart .base-content .cart-item {
  background: #fff;
}
.ShopCart .base-content .cart-item .check {
  width: 16px;
  height: 16px;
  margin-left: 15px;
}
.ShopCart .base-content .cart-item .goods-info {
  border-bottom: 1px solid #eee;
  padding: 10px;
}
.ShopCart .base-content .cart-item .goods-info .goods-img {
  width: 75px;
  height: 75px;
  margin-right: 2px;
}
.ShopCart .base-content .cart-item .goods-info .title {
  padding-top: 5px;
  height: 40px;
  line-height: 20px;
  font-size: 14px;
  color: #333;
}
.ShopCart .base-content .cart-item .goods-info .bottom-info {
  margin-top: 15px;
}
.ShopCart .base-content .cart-item .goods-info .bottom-info .spec {
  font-size: 14px;
  color: #999;
  height: 22px;
  line-height: 22px;
}
.ShopCart .base-content .cart-item .goods-info .bottom-info .num {
  color: #999;
  font-size: 14px;
}
.ShopCart .base-content .cart-item .goods-info .bottom-info .am-stepper {
  width: 100px;
  height: 22px;
  line-height: 22px;
  padding: 0;
}
.ShopCart .base-content .cart-item .goods-info .bottom-info .am-stepper-input-wrap {
  height: 22px;
  line-height: 22px;
}
.ShopCart .base-content .cart-item .goods-info .bottom-info .am-stepper-input-wrap .am-stepper-input {
  width: 30px;
}
.ShopCart .base-content .cart-item .goods-info .bottom-info .am-stepper-handler {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  line-height: 20px;
}
.ShopCart .base-content .cart-item .goods-info .bottom-info .am-stepper-handler-up svg {
  fill: #e21b1a;
}
.ShopCart .base-content .cart-item .goods-info .price {
  padding-top: 8px;
  font-size: 13px;
  font-weight: 600;
  text-align: right;
  padding-left: 10px;
  padding-right: 5px;
}
.ShopCart .footer {
  height: 50px;
  background: #fff;
  padding: 0 15px;
  color: #666;
}
.ShopCart .footer .am-checkbox-wrapper .am-checkbox {
  margin-right: 10px;
  margin-bottom: 1px;
}
.ShopCart .footer .total {
  font-size: 14px;
  margin-right: 10px;
}
.ShopCart .footer .total em {
  font-size: 16px;
  color: #e21b1a;
  font-weight: 500;
}
.ShopCart .footer .pay-btn {
  background: #e21b1a;
  color: #fff;
  height: 36px;
  line-height: 36px;
}
.ShopCart .footer .del-btn {
  background: #fff;
}
.ShopCart .footer .del-btn.am-button {
  border: 1px solid #e21b1a;
  color: #e21b1a;
}



