* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
  height: 20px;
}
.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #e21b1a;
  background: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-underline {
  border-color: #e21b1a;
}
.Hots .am-carousel {
  height: 100px;
}
.Hots .slider-decorator-0 {
  opacity: 0;
  padding-right: 15px;
  bottom: 10px !important;
}
.Hots .slider-decorator-0 .am-carousel-wrap {
  text-align: right;
}
.Hots .anchor-recommend {
  padding: 15px 0;
  background: #fff;
  -webkit-box-shadow: inset 0 0 0 0 #F3F3F3;
  text-align: center;
}
.Hots .anchor-recommend span {
  position: relative;
  font-size: 13px;
  color: #666;
}
.Hots .anchor-recommend span::before,
.Hots .anchor-recommend span::after {
  content: "";
  width: 16px;
  height: 1px;
  background: #B6B6B6;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
      transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
}
.Hots .anchor-recommend span::before {
  left: -21px;
}
.Hots .anchor-recommend span::after {
  right: -21px;
}



