* {
  outline: none !important;
  font-family: '微软雅黑';
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-text-size-adjust: none;
}
ul,
ol,
dl,
li {
  list-style: none;
}
em {
  font-style: normal;
}
a {
  text-decoration: none;
}
html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  background: #f3f3f3;
}
.base-line {
  position: relative;
}
.base-line:after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  content: '';
  background-color: #f5f5f5;
}
.base-content {
  height: calc(100vh - 45px);
  overflow: scroll;
}
.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: off*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.am-navbar img {
  height: 20px;
}
.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #e21b1a;
  background: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.nav-tabs .am-tabs-default-bar-underline {
  border-color: #e21b1a;
}
.ShopIndex .nav-tabs .am-tabs-default-bar {
  background: transparent !important;
}
.ShopIndex .nav-tabs .am-tabs-default-bar-tab {
  color: #333;
}
.ShopIndex .search-con {
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.ShopIndex .search-con .am-navbar-left,
.ShopIndex .search-con .am-navbar-right {
  -ms-flex: none;
      flex: none;
}
.ShopIndex .search-con .am-navbar-left span,
.ShopIndex .search-con .am-navbar-right span {
  display: inline-block;
}
.ShopIndex .search-con .am-navbar-left .right-cont,
.ShopIndex .search-con .am-navbar-right .right-cont {
  width: 100%;
  height: 100%;
  padding-right: 8px;
}
.ShopIndex .search-con .am-navbar-left .am-search-input,
.ShopIndex .search-con .am-navbar-right .am-search-input {
  color: red;
}
.ShopIndex .search-con .am-navbar-left .am-navbar-left-icon {
  margin-right: 0;
}
.ShopIndex .search-con .am-navbar-left .am-navbar-left-icon img {
  height: 14px;
}
.ShopIndex .search-con .am-navbar-title {
  width: 86%;
}
.ShopIndex .search-con .am-search {
  width: 100%;
  background: #fff;
}
.ShopIndex .search-con .am-search .am-search-input {
  background: #f3f4f5;
  border-radius: 15px;
}
.ShopIndex .search-con .am-search .am-search-input .am-search-synthetic-ph-placeholder {
  font-size: 13px;
}
.ShopIndex .search-con .am-search .am-search-cancel {
  display: none;
}
.ShopIndex .search-con .am-badge-text {
  padding: 0 3px;
  height: 14px;
  line-height: 14px;
  font-size: 10px;
}



